<!-- eslint-disable max-len -->
<template>
  <aside class="filter">
        <h2 class="filter__title">Фильтры</h2>

        <form class="filter__form form" action="#" method="get" @submit.prevent="submit">
          <fieldset class="form__block">
            <legend class="form__legend">Цена</legend>
            <label class="form__label form__label--price" for="inp1">
              <input id="inp1" class="form__input" type="text" name="min-price" v-model.number="currentPriceFrom">
              <span class="form__value">От</span>
            </label>
            <label class="form__label form__label--price" for="inp2">
              <input id="inp2" class="form__input" type="text" name="max-price" v-model.number="currentPriceTo">
              <span class="form__value">До</span>
            </label>
          </fieldset>

          <fieldset class="form__block">
            <legend class="form__legend">Категория</legend>
            <label id="inp3" class="form__label form__label--select" for="inp3">
              <select class="form__select" name="category" v-model.number="currentCategoryId">
                <option value="0">Все категории</option>
                <option :value="category.id" v-for="category in categories" :key="category.id">{{ category.title }}</option>
              </select>
            </label>
          </fieldset>

          <fieldset class="form__block">
            <legend class="form__legend">Цвет</legend>
            <ul class="colors">
              <li class="colors__item" v-for="item_color in colors" :key="item_color.id">
                <label class="colors__label" :for="'inpput_color_' + item_color.id">
                  <input :id="'inpput_color_' + item_color.id" class="colors__radio sr-only" type="radio" name="color" :value="item_color.id" v-model="currentColor">
                  <span class="colors__value" :style="{'background-color': item_color.code}">
                  </span>
                </label>
              </li>
            </ul>
          </fieldset>

          <fieldset class="form__block">
            <legend class="form__legend">Объемб в ГБ</legend>
            <ul class="check-list">
              <li class="check-list__item">
                <label class="check-list__label" for="inp11">
                  <input id="inp11" class="check-list__check sr-only" type="checkbox" name="volume" value="8" checked="">
                  <span class="check-list__desc">
                    8
                    <span>(313)</span>
                  </span>
                </label>
              </li>
              <li class="check-list__item">
                <label class="check-list__label" for="inp12">
                  <input id="inp12" class="check-list__check sr-only" type="checkbox" name="volume" value="16">
                  <span class="check-list__desc">
                    16
                    <span>(461)</span>
                  </span>
                </label>
              </li>
              <li class="check-list__item">
                <label class="check-list__label" for="inp13">
                  <input id="inp13" class="check-list__check sr-only" type="checkbox" name="volume" value="32">
                  <span class="check-list__desc">
                    32
                    <span>(313)</span>
                  </span>
                </label>
              </li>
              <li class="check-list__item">
                <label class="check-list__label" for="inp14">
                  <input id="inp14" class="check-list__check sr-only" type="checkbox" name="volume" value="64">
                  <span class="check-list__desc">
                    64
                    <span>(313)</span>
                  </span>
                </label>
              </li>
              <li class="check-list__item">
                <label class="check-list__label" for="inp15">
                  <input id="inp15" class="check-list__check sr-only" type="checkbox" name="volume" value="128">
                  <span class="check-list__desc">
                    128
                    <span>(313)</span>
                  </span>
                </label>
              </li>
              <li class="check-list__item">
                <label class="check-list__label" for="inp16">
                  <input id="inp16" class="check-list__check sr-only" type="checkbox" name="volume" value="264">
                  <span class="check-list__desc">
                    264
                    <span>(313)</span>
                  </span>
                </label>
              </li>
            </ul>
          </fieldset>

          <button class="filter__submit button button--primery" type="submit">
            Применить
          </button>
          <button class="filter__reset button button--second" type="button" @click.prevent="reset">
            Сбросить
          </button>
        </form>
      </aside>
</template>
<!-- eslint-enable max-len -->

<script>
import axios from 'axios';
import API_BASE_URL from '../store/config';

export default {
  props: ['priceFrom', 'priceTo', 'categoryId', 'color'],
  data() {
    return {
      modelPriceFrom: null,
      modelPriceTo: null,
      modelCategoryId: null,

      currentPriceFrom: 0,
      currentPriceTo: 0,
      currentCategoryId: 0,
      currentColor: '#73B6EA',

      categoriesData: null,
      colorsData: null,
    };
  },
  computed: {
    categories() {
      return this.categoriesData ? this.categoriesData.items : [];
    },
    colors() {
      return this.colorsData ? this.colorsData : [];
    },
  },
  mounted() {
    this.setProps();
  },
  watch: {
    priceFrom(value) {
      this.currentPriceFrom = value;
    },
    priceTo(value) {
      this.currentPriceTo = value;
    },
    priceCategoryId(value) {
      this.currentCategoryId = value;
    },
    color(value) {
      this.currentColor = value;
    },
  },
  methods: {
    submit() {
      this.$emit('updatePriceFrom', this.currentPriceFrom);
      this.$emit('updatePriceTo', this.currentPriceTo);
      this.$emit('updateCategoryId', this.currentCategoryId);
      this.$emit('updateColor', this.currentColor);
    },
    reset() {
      this.currentCategoryId = 0;
      this.$emit('updatePriceFrom', 0);
      this.$emit('updatePriceTo', 0);
      this.$emit('updateCategoryId', this.currentCategoryId);
      this.$emit('updateColor', 0);
    },
    setProps() {
      this.modelPriceFrom = this.priceFrom;
      this.modelPriceTo = this.priceTo;
      this.modelCategoryId = this.categoryId;
    },
    loadCategories() {
      axios.get(`${API_BASE_URL}/api/productCategories`)
        .then((response) => {
          this.categoriesData = response.data;
        });
    },
    loadColors() {
      axios.get(`${API_BASE_URL}/api/colors`)
        .then((response) => {
          this.colorsData = response.data.items;
        });
    },
  },
  created() {
    this.loadCategories();
    this.loadColors();
  },
};
</script>
