<!-- eslint-disable max-len -->
<template>
  <main class="content container">
    <div class="content__top content__top--catalog">
      <h1 class="content__title">
        Каталог
      </h1>
      <span class="content__info">
        152 товара
      </span>
    </div>

    <div class="content__catalog">
      <ProductFilter :price-from="filterPriceFrom" :price-to="filterPriceTo" :category-id="filterCategoryId" :color="filterColor" @updatePriceFrom="updatePriceFrom" @updatePriceTo="updatePriceTo" @updateCategoryId="updateCategoryId" @updateColor="updateColor"></ProductFilter>

      <section class="catalog" style="align-items: center">

        <div v-if="productsLoading">Загрузка товаров...</div>
        <div v-if="productsLoadingFailed">Произошла ошибка при загрузке товаров! <button @click.prevent="loadProducts">Попробовать еще раз</button></div>

        <ProductList :products="products" @gotoPage="(pageName, pageParams) => $emit('gotoPage', pageName, pageParams)"></ProductList>
        <BasePagination :page="page" :count="countProducts" :per-page="productsPerPage" @paginate="methodPaginateApp"></BasePagination>

      </section>
    </div>
  </main>

</template>
<!-- eslint-enable max-len -->

<!-- eslint-disable max-len -->
<script>
import ProductList from '@/components/ProductList.vue';
import BasePagination from '@/components/BasePagination.vue';
import ProductFilter from '@/components/ProductFilter.vue';
import axios from 'axios';
import API_BASE_URL from '../store/config';

export default {
  components: { ProductList, BasePagination, ProductFilter },
  data() {
    return {
      filterPriceFrom: 0,
      filterPriceTo: 0,
      filterCategoryId: 0,
      filterColor: 0,

      page: 1,
      productsPerPage: 3,
      productsData: null,

      loadProductsTimer: null,
      productsLoading: false,
      productsLoadingFailed: false,
    };
  },
  computed: {
    products() {
      if (!this.productsData) {
        return [];
      }

      return this.productsData.items.map((product) => (
        {
          ...product,
          image: product.image.file.url,
        }
      ));
    },
    countProducts() {
      return this.productsData ? this.productsData.pagination.total : 0;
    },
  },
  methods: {
    updatePriceFrom(value) {
      this.filterPriceFrom = value;
    },
    updatePriceTo(value) {
      this.filterPriceTo = value;
    },
    updateCategoryId(value) {
      this.filterCategoryId = value;
    },
    methodPaginateApp(pageNew) {
      this.page = pageNew;
    },
    updateColor(newColor) {
      console.log('updateColor:', newColor);
      this.filterColor = newColor;
    },
    loadProducts() {
      clearTimeout(this.loadProductsTimer);
      this.productsLoading = true;
      this.productsLoadingFailed = false;
      this.loadProductsTimer = setTimeout(() => {
        axios.get(`${API_BASE_URL}/api/products`, {
          params: {
            page: this.page,
            limit: this.productsPerPage,
            categoryId: this.filterCategoryId,
            minPrice: this.filterPriceFrom,
            maxPrice: this.filterPriceTo,
            colorId: this.filterColor,
          },
        })
          .then((response) => {
            this.productsData = response.data;
          })
          .catch(() => { this.productsLoadingFailed = true; })
          .then(() => { this.productsLoading = false; });
      }, 2000);
    },
  },
  watch: {
    page() {
      this.loadProducts();
    },
    filterPriceFrom() {
      this.loadProducts();
    },
    filterPriceTo() {
      this.loadProducts();
    },
    filterCategoryId() {
      this.loadProducts();
    },
    filterColor() {
      this.loadProducts();
    },
  },
  created() {
    this.loadProducts();
  },
};
</script>
<!-- eslint-enable max-len -->
