<!-- eslint-disable max-len -->
<template>
  <li class="catalog__item">
      <router-link class="catalog__pic" :to="{ name:'product', params: { id: product.id } }">
        <img :src="product.image" :alt="product.title">
      </router-link>

      <h3 class="catalog__title">
        <a href="#">
          {{ product.title}}
        </a>
      </h3>

      <span class="catalog__price">
        {{ priceFormat(product.price) }} ₽
      </span>
      <ul class="colors colors--black">
        <li class="colors__item" v-for="product_color in product.colors" :key="product_color.id">
          <label class="colors__label" :for="'input_ ' + product.id + '_' + product_color.id">
            <input class="colors__radio sr-only" :id="'input_ ' + product.id + '_' + product_color.id"
                   type="radio" :value="product_color.id" v-model="color">
            <span class="colors__value" :style="{'background-color': product_color.code}">
            </span>
          </label>
        </li>
      </ul>
    </li>
</template>
<!-- eslint-enable max-len -->

<script>
export default {
  data() {
    return {
      color: '#73B6EA',
    };
  },
  props: ['product'],
  methods: {
    priceFormat(number) {
      const result = new Intl.NumberFormat().format(number);
      return result;
    },
  },
  mounted() {
    if (this.product.colors[0]) {
      const firstColor = this.product.colors[0];
      this.color = firstColor.id;
    }
  },
};
</script>
