import { createWebHistory, createRouter } from 'vue-router';
import MainPage from '@/pages/MainPage.vue';
import ProductPage from '@/pages/ProductPage.vue';
import NotFoundPage from '@/pages/NotFoundPage.vue';
import CartPage from '@/pages/CartPage.vue';
import OrderPage from '@/pages/OrderPage.vue';
import OrderInfoPage from '@/pages/OrderInfoPage.vue';

const routes = [
  // { name: 'main', component: MainPage, path: '/' },
  // { name: 'product', component: ProductPage, path: '/product/:id' },
  // { name: 'cart', component: CartPage, path: '/cart' },
  // { name: 'order', component: OrderPage, path: '/order' },
  // { name: 'orderInfo', component: OrderInfoPage, path: '/order/:id' },

  { name: 'main', component: MainPage, path: '/vue-app/' },
  { name: 'product', component: ProductPage, path: '/vue-app/product/:id' },
  { name: 'cart', component: CartPage, path: '/vue-app/cart' },
  { name: 'order', component: OrderPage, path: '/vue-app/order' },
  { name: 'orderInfo', component: OrderInfoPage, path: '/vue-app/order/:id' },

  { name: 'NotFoundPage', component: NotFoundPage, path: '/:pathMatch(.*)*' },

];

const myrouter = createRouter({
  history: createWebHistory(),
  routes,
});

export default myrouter;
