<!-- eslint-disable max-len -->
<template>
  <li class="cart__item product">
    <div class="product__pic">
      <img :src="item.product.image.file.url" width="120" height="120" :alt="item.product.title">
    </div>
    <h3 class="product__title">
      {{item.product.title}}
    </h3>

    <span class="product__code">
                Артикул: {{ item.productId }}
              </span>

    <div class="product__counter form__counter">
      <button type="button" aria-label="Убрать один товар" @click.prevent="--amount" style="cursor: pointer">
        <svg width="10" height="10" fill="currentColor">
          <use xlink:href="#icon-minus"></use>
        </svg>
      </button>

      <label for="Each_form_element">
        <input type="text" v-model.number="amount" name="count" id="Each_form_element">
      </label>

      <button type="button" aria-label="Добавить один товар" @click.prevent="++amount" style="cursor: pointer">
        <svg width="10" height="10" fill="currentColor">
          <use xlink:href="#icon-plus"></use>
        </svg>
      </button>
    </div>

    <b class="product__price">
      {{ priceFormat(item.amount * item.product.price ) }} ₽
    </b>

    <button class="product__del button-del" type="button" aria-label="Удалить товар из корзины" @click="deleteCartProduct(item.productId)" style="cursor: pointer">
      <svg width="20" height="20" fill="currentColor">
        <use xlink:href="#icon-close"></use>
      </svg>
    </button>
  </li>
</template>

<script>
import { mapActions } from 'vuex';

export default {
  props: ['item'],
  computed: {
    amount: {
      get() {
        return this.item.amount;
      },
      set(value) {
        this.$store.dispatch('updateCartProductAmount', { productId: this.item.productId, amount: value });
      },
    },
  },
  methods: {
    ...mapActions(['deleteCartProduct']),

    priceFormat(number) {
      const result = new Intl.NumberFormat().format(number);
      return result;
    },
  },
};
</script>
